	uniIcon
} from '@dcloudio/uni-ui/lib/uni-icon/uni-icon.vue'
export default {
	data() {
		return {
			title1: '全部',
			title2: '全部',
			title3: '全部',
			active1: 1,
			active2: 1,
			active3: 1,
			show: false,
			show1: false,
			show2: false,
			show3: false,
			checkAll: false,
			lookSelects:false,
			wordCount:0,
			list: [{
					courseTitle: '四级词汇核心词汇',
					chapter: 'chapter 1-Lesson 2',
					wordList: [{
							'word': 'accessory',
							'wordId': 1,
							'Interpretation': 'n.同谋，从犯；附件'
						},
						{
							'word': 'accidental',
							'wordId': 2,
							'Interpretation': 'a.偶然的；非本质的'
						},
						{
							'word': 'accommodate',
							'wordId': 3,
							'Interpretation': 'vt.容纳；供应，供给'
						},
						{
							'word': 'accordance',
							'wordId': 4,
							'Interpretation': 'n.一致；和谐；授予'
						},
						{
							'word': 'accordingly',
							'wordId': 5,
							'Interpretation': 'ad.因此，所以；照着'
						},

					]
				},
				{
					courseTitle: '六级核心词汇',
					chapter: 'chapter 5-Lesson 13',
					wordList: [{
							'word': 'bacon',
							'wordId': 6,
							'Interpretation': 'n.实例，情况，建议；vt.举...为例'
						},
						{
							'word': 'ability',
							'wordId': 7,
							'Interpretation': 'vt.容纳；供应，供给'
						},
						{
							'word': 'involve',
							'wordId': 8,
							'Interpretation': 'a.偶然的；非本质的'
						},
						{
							'word': 'jail',
							'wordId': 9,
							'Interpretation': 'ad.因此，所以；照着'
						},
						{
							'word': 'aboard',
							'wordId': 10,
							'Interpretation': 'n.一致；和谐；授予'
						},

					]
				}
			],
			result: [],
			thesaurusLists: [],
			chapterLists: [],
			lessonLists: []

		};
	},
	watch: {
		result() {

			console.log(this.checkAll)
		}
	},
	components: {
		uniIcon
	},
	watch: {
		title1() {
			console.log(this.title1)
			this.chapterList(this.title1)
			this.title2 = "全部"
			this.title3 = "全部"
		},
		title2() {
			this.lessonList(this.title1, this.title2)
		}
	},
	methods: {
		actives1() {
			if (this.show == true && (this.show2 == true || this.show3 == true)) {
				this.show = this.show
				console.log(1)
			} else if (this.show == true && this.show1 == false) {
				this.show = false
				console.log(2)
			} else {
				this.show = !this.show
				console.log(3)
			}
			console.log(this.show)
			this.active1 = !this.active1
			this.show1 = !this.show1
			this.show2 = false
			this.show3 = false
			this.active2 = false
			this.active3 = false
			console.log(this.active1, 'active1')
		},
		actives2() {
			if (this.show == true && (this.show1 == true || this.show3 == true)) {
				this.show = this.show
			} else {
				this.show = !this.show
			}

			this.active2 = !this.active2
			this.show2 = !this.show2
			this.show1 = false
			this.show3 = false
			this.active1 = false
			this.active3 = false
			console.log(this.active2, 'active2')
		},
		actives3() {
			if (this.show == true && (this.show1 == true || this.show2 == true)) {
				this.show = this.show
			} else {
				this.show = !this.show
			}

			this.show1 = false
			this.show2 = false
			this.active2 = false
			this.active1 = false
			this.active3 = !this.active3
			this.show3 = !this.show3
			console.log(this.active3)
		},
		optionsActive1(val) {
			this.title1 = val
			console.log(this.title1, this.thesaurusId1)
			this.show = !this.show
			this.show1 = false
			this.active1 = !this.active1
		},
		optionsActive2(val) {
			this.title2 = val
			console.log(val)
			this.show = !this.show
			this.show2 = false
			this.active2 = !this.active2
		},
		optionsActive3(val) {
			this.title3 = val
			console.log(val)
			this.show = !this.show
			this.show3 = false
			this.active3 = !this.active3
		},
		// 全选
		onChange(event) {
			if (event.detail == false) {
				this.result = []
			}
			if (event.detail == true) {
				this.list.forEach(data => {
					console.log(data)
					data.wordList.forEach(val => {
						this.result.push(val.wordId.toString())
					})
				})
				this.wordCount=this.result.length
			}
			console.log(this.result)
			this.checkAll = event.detail
			this.lookSelects=event.detail
		},
		onChanges(event) {
			this.result = event.detail
			console.log(this.result)
			if (this.result.length == 0) {
				this.lookSelects = false
			} else {
				this.lookSelects = true
				this.wordCount=this.result.length
			}
		},
		// 获取课程下拉列表
		thesaurusList() {
			this.$minApi.thesaurusList({}).then(data => {
				console.log(data)
				this.thesaurusLists = data.data
			})
		},
		// 获取章下拉列表
		chapterList(thesauruName) {
			this.$minApi.chapterList({
				thesauruName: thesauruName
			}).then(data => {
				console.log(data)
				this.chapterLists = data.data
			})
		},
		// 获取节下拉列表
		lessonList(thesauruName, chapter) {
			console.log(chapter, thesauruName)
			this.$minApi.lessonList({
				chapter: chapter,
				thesauruName: thesauruName
			}).then(data => {
				console.log(data)
				this.lessonLists = data.data
			})
		}
	},
	created() {
		this.thesaurusList()
	}
}
